.nameList {
    color: #000;
}

.list-group-item a:hover{
    text-decoration: none !important;
}

.reviewBtn {
    background: #45484d; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom, #45484d 0%,#000000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    color: #fff;
    padding: 15px;
    font-size: 20px;
    cursor: pointer;
}

form h3 {
    text-align: center;
    text-transform: uppercase;
    margin: 10px;
}

.mainInfo {
    background: rgb(242,245,246); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(242,245,246,1) 0%,rgba(227,234,237,1) 37%,rgba(200,215,220,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2f5f6', endColorstr='#c8d7dc',GradientType=0 ); /* IE6-9 */

}
.modalWindow {
    position: fixed;
    left: 50%;
    top: 50%;
    max-width: 500px;
    width: 100%;
    margin-left: -250px;
    margin-top: -200px;
    z-index: 999;
  }
  
  .modalWindow::before {
    content: "";
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.2;
  }
  
  .modalWindow form {
    position: relative;
    z-index: 2;
    background-color: #fff;
    padding: 40px;
    box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
  }
