.modalWindow {
    position: fixed;
    left: 50%;
    top: 50%;
    max-width: 500px;
    width: 100%;
    margin-left: -250px;
    margin-top: -200px;
    z-index: 999;
  }
  
  .modalWindow::before {
    content: "";
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.2;
  }
  
  .modalWindow form {
    position: relative;
    z-index: 2;
    background-color: #fff;
    padding: 40px;
    box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
  }